header {
    background: #2a5298;
    border-bottom: 2px solid white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.8em;
}

.app-header {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
}

nav a {
    margin: .5em;
}

nav a:active {
    color: blue;
}

nav a.active {
    color: #e8e8e8;
    font-weight: bold;
    text-decoration: overline;
}

.logged-user-header-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
}

.log-btn {
    background-color: #2a5298;
    color: #ffffff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.log-btn:hover,
.log-btn:focus {
    background-color: #1d3a6e;
}


@media screen and (max-width: 768px) {
    .log-btn {
        /* Adjust size and spacing for smaller screens */
        /* padding: 1px;
      width: 1em; */
    }
}

.login-text {
    font-family: Arial, sans-serif;
    font-size: 14px;
    color: #888888;
    text-align: center;
    margin-top: 10px;
    padding: 5px 0;
}

.login-text:hover {
    cursor: pointer;
    text-decoration: underline;
}

form.credentials-form {
    display: flex;
    gap: 0.2em;
    align-items: center;
}


/* INDEX */
.toy-title {
    text-align: center;
    /* font-weight: bold; */
}

.home-container {
    margin: auto;
    text-align: center;
}

.home-container img {
    max-width: 35em;
}

/* FILTER */
.toy-filter-container {
    background-color: #f5f5f5;
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 10px;
    border: 1px solid #cccccc;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
    justify-content: space-between;
}




/* FOOTER */
.app-footer {
    text-align: center;
}





/* TODO LIST */
.todo-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.todo-title {
    text-align: center;
}




/* TODO PREVWIEW */
.todo-preview {
    padding: 0.2em;
    margin-bottom: 10px;
    background-color: #f5f5f5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.3);
}

.todo-list .options {
    display: flex;
    align-items: center;
    gap: 0.5em;
}




/* Todo Text and Labels */
.todo-txt {
    padding: 5px;
    margin-bottom: 5px;
    display: flex;
    gap: 0.1em;
}

.todo-lable-txt {
    font-size: 16px;
    cursor: pointer;
}



/* Buttons and Icons */
.btn {
    padding: 5px;
    margin-right: 5px;
    background-color: #2a5298;
    color: #ffffff;
    border-radius: 5px;
}

/* Icon styling */
.fa-solid {
    font-size: 1.1em;
    text-align: center;
    width: 0.9em;
}


.completed-task {
    text-decoration: line-through;
    color: #888888;
}




/* TODOEDIT */
.todo-edit {
    text-align: center;
}

.todo-edit form {
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}

.todo-edit .btns-section {
    position: relative;
    left: 1.3em;
}


/* TODO DETAILS */
.todo-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5em;
}