.toy-preview {
    text-align: center;
    padding: 0.3em;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
}

.toy-preview h4 {
    text-transform: capitalize;
    color: black;
}

.toy-preview img {
    max-width: 8em;
    margin: auto;
    padding: 0.5em;
}

.toy-preview-content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    flex-grow: 1;
}