.user-msg {
    background-color: rgba(169, 94, 32, 0.753);
    width: 5em;
    height: 5em;
    padding: .2em .5em;
    position: fixed;
    top: 3em;
    right: 3em;
    color: rgb(3, 3, 3);
    border-radius: 1em;
}

.user-msg.success {
    background-color: rgb(74, 134, 42);
}

.user-msg.error {
    background-color: rgb(209, 41, 41);
}

.user-msg button {
    font-size: .5em;
    float: right;
    margin: .5em;
}